@font-face {
    font-family: 'NotoSansCJKkr-Medium';
	src : url(../fonts/NotoSansCJKkr-Medium.otf) format('truetype');
}

html, body {
    height: 100%;
	font-family : NotoSansCJKkr-Medium
}

#header{
    border-bottom : 1px solid black;
    height: 65px;
    position: fixed;
    top :0;
    width : 100%;
    background-color : #ffffff;
    z-index : 10;
}


#blogTitle{
    position: absolute;
    top:1.5%;
    left:5%;
}

#blogTitle a{
    color: black; 
    text-decoration: none;
}

#navMenu{
    position: absolute;
    top:1.5%;
    right:5%;
    list-style-type: none;
}

#navMenu li{
    display: inline-block;
    padding:0px 30px;
}

#navMenu li a{
    color: black; 
    text-decoration: none;
}

.content {
    color : gray;
}

#mainSection{
    display: flex;
    height: 85%;
    background-color: #ffffff;
}

#joelPicture{
    width:400px;
    height:400px;
    margin:auto;
}

#sideMenuBar {
    position : fixed;
    top: 65px;
    height : 90%;
    width : 300px;
    border-right: 2px solid black;
    background-color: black;
}

#sideMenuBar ul li{
    list-style-type: none;
    color : white;
    padding : 20px;
    font-size: 20px;
}

.childMenu li{
    font-size : 10px;
}

#postList{
    position : absolute;
    top: 100px;
    left: 400px;
}

#postStyle{
    position: relative;
    padding: 0px 100px;
}

blockquote {
    display:block;
    background: #fff;
    margin: 0px 50px 0px 50px;
    padding: 30px 30px 30px 50px;
    position: relative;
    font-family: Georgia, serif;
    font-size: 18px;
    line-height: 1.2;
    color: #666;
    text-align: justify;
    border-left: 15px solid #c76c0c;
    border-right: 5px solid #c76c0c;
    -moz-box-shadow: 2px 2px 15px #ccc;
    -webkit-box-shadow: 2px 2px 15px #ccc;
    box-shadow: 2px 2px 15px #ccc;
}